function get(url) {
  if (apis[url])
    return new Promise((resolve, reject) => {
      apis[url]()
        .then(({default: d}) => resolve(d))
        .catch(e => reject(e));
    });
  else return Promise.reject(new Error(`${url} not found.`));
}
export default { get }
